import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import PageTitle from "../components/PageTitle";
import { getBoardDetail, deleteBoardRequest } from "../apis";
import { useCookies } from 'react-cookie';
import { useLoginuserStore } from "../stores/store";

export default function BoardDetail() {
  const [data, setData] = useState(null); //    
  const location = useLocation(); //  URL  
  const searchParams = new URLSearchParams(location.search); //   
  const boardNumber = searchParams.get("board_number"); // 'board_number'  
  const writeDatetime = location.state?.writeDatetime;  //   
  const [cookies] = useCookies(['accessToken']);
  const navigate = useNavigate();
  const { loginUser } = useLoginuserStore();

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await getBoardDetail(boardNumber);
        if (response) {
          console.log(response)
          setData(response);
          console.log('  :', response);
        }
      } catch (error) {
        console.error("    :", error);
      }
    };
    
    if (boardNumber) {
      fetchData();
    }
  }, [boardNumber]); // boardNumber   

  const handleEdit = () => {
    navigate(`/board/edit/${boardNumber}`);
  };

  const handleDelete = async () => {
    if (!cookies.accessToken) {
        alert(' .');
        return;
    }

    if (!window.confirm('   ?')) return;

    try {
        const response = await deleteBoardRequest(boardNumber, cookies.accessToken);
        console.log('Delete response:', response);
        
        if (response.code === 'SU') {
            alert(' .');
            //        
            const prevPath = document.referrer;
            if (prevPath && prevPath.includes(window.location.origin)) {
                window.history.back();
                console.log('  ');
            } else {
                navigate('/');
            }
        } else {
            alert('  .');
        }
    } catch (error) {
        console.error('   :', error);
        if (error.response?.status === 403) {
            alert('  .');
        } else {
            alert('  .');
        }
    }
  };

  return (
    <div>
      
        {data ? (
          <div className="bg-white shadow-lg rounded-lg overflow-hidden">
            <div className="p-6 border-b border-gray-200">
              <h1 className="text-xl sm:text-3xl font-bold text-gray-900 mb-2">
                {data.title}
              </h1>
              <div className="flex flex-wrap items-center text-sm text-gray-500 gap-4">
                <div className="flex items-center">
                  <svg className="w-4 h-4 mr-1" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
                  </svg>
                  {writeDatetime || ' '}
                </div>
              </div>
            </div>

            {/*   */}
            <div className="p-6">
              <div
                className="prose max-w-none text-gray-800"
                dangerouslySetInnerHTML={{ __html: data.content }}
              />
            </div>

            {/*   */}
            {cookies.accessToken && (
              <div className="px-6 py-4 bg-gray-50 border-t border-gray-200">
                <div className="flex justify-end space-x-3">
                  <button
                    onClick={handleEdit}
                    className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
                  >
                    <svg className="w-4 h-4 mr-1.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z" />
                    </svg>
                    
                  </button>
                  <button
                    onClick={handleDelete}
                    className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-red-600 hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500"
                  >
                    <svg className="w-4 h-4 mr-1.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                    </svg>
                    
                  </button>
                </div>
              </div>
            )}
          </div>
        ) : (
          <div className="text-center py-12">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-gray-900 mx-auto"></div>
            <p className="mt-4 text-gray-600">  ...</p>
          </div>
        )}
      </div>
    
  );
}
