import React, { useState, useRef, useEffect, useMemo, useCallback } from "react";
import ReactQuill, { Quill } from "react-quill-new";
import 'react-quill-new/dist/quill.snow.css'; // quill  
import { postBoardRequest, uploadImage, uploadFileRequest, getImageUrl } from "../apis";
import { useNavigate } from "react-router-dom";
import { useCookies } from "react-cookie";
import { useBoardStore, useLoginuserStore } from "../stores/store";
import ImageResize from 'quill-image-resize';

// Quill ImageResize  
Quill.register('modules/imageResize', ImageResize);

export default function BoardWrite() {
  const quillRef = useRef();
  const navigate = useNavigate();
  const [cookies] = useCookies(['accessToken']);
  const { title, setTitle, content, boardImageList, setBoardImageList, category, setCategory, setContent, resetBoard } = useBoardStore();
  const { loginUser, setLoginUser, resetLoginUser } = useLoginuserStore();
  const [uploadedUrls, setUploadedUrls] = useState([]); //   URL 
  const [memberName, setMemberName] = useState('');
  const [memberEmail, setMemberEmail] = useState('');
  const [memberDegree, setMemberDegree] = useState('');
  const [memberImage, setMemberImage] = useState('');
  const [paperTitle, setPaperTitle] = useState('');
  const [authors, setAuthors] = useState('');
  const [publishDate, setPublishDate] = useState('');
  const [paperLink, setPaperLink] = useState('');

  const imageHandler = useCallback(() => {
    const input = document.createElement('input');
    input.setAttribute('type', 'file');
    input.setAttribute('accept', 'image/*');
    
    input.onchange = async () => {
      const file = input.files[0];
      if (file) {
        try {
          const imageUrl = await uploadImage(file, cookies.accessToken);
          console.log('  URL:', imageUrl);
          
          if (imageUrl) {
            // Members Alumni   API  
            if (category === 'Members' || category === 'Alumni') {
              const apiImageUrl = `/api/v1/images/${imageUrl}`;
              console.log('API  URL:', apiImageUrl);
              setMemberImage(apiImageUrl);
              console.log('memberImage:', memberImage);
              //     memberData 
              const memberData = {
                name: memberName,
                email: memberEmail,
                degree: memberDegree,
                image: apiImageUrl
              };
              setContent(JSON.stringify(memberData));
            }
            
            //    URL
            const fullUrl = `https://jbnuvetmedimmunelab.store/api/v1/images/${imageUrl}`;
            const quillEditor = quillRef.current.getEditor();
            const range = quillEditor.getSelection(true);
            quillEditor.insertEmbed(range.index, 'image', fullUrl);
            
            setUploadedUrls(prev => [...prev, imageUrl]);
          }
        } catch (error) {
          console.error('  :', error);
          alert('  .');
        }
      }
    };
    input.click();
  }, [cookies.accessToken, category, memberName, memberEmail, memberDegree]);

  // modules  
  const modules = useMemo(() => ({
    toolbar: {
      container: [
        [{ header: [1, 2, 3, false] }],
        ["bold", "italic", "underline", "strike"],
        ["blockquote", "code-block"],
        [{ list: "ordered" }, { list: "bullet" }],
        [{ align: [] }],
        ["link", "image"],  //  
        [{ size: ["small", false, "large", "huge"] }],  //  
        ["clean"]
      ],
      handlers: {
        image: imageHandler,
      }
    },
    imageResize: {
      displaySize: true,
      modules: ['Resize', 'DisplaySize']
    },
    clipboard: {
      matchVisual: false
    }
  }), [imageHandler]);

  //  
  const formats = [
    'header',
    'bold', 'italic', 'underline', 'strike',
    'blockquote', 'code-block',
    'list', 'bullet',
    'align',
    'link', 'image',
    'size',
    'clean'
  ];

  //  API  
  const postBoardResponse = (responseBody) => {
    if (!responseBody) return;
    const { code } = responseBody;
    if (code === 'AF' || code === 'NU') {
      navigate('/');
    }
    if (code === 'VF') alert('  .');
    if (code === 'DE') alert(' .');
    if (code !== 'SU') return;
    
    alert(' !');
    resetBoard();
    setTitle('');
    setCategory('');
    setContent('');

    const quillEditor = quillRef.current.getEditor();
    quillEditor.setText(''); //   
    if (!loginUser) return;
  };

  //     
  const handleSubmit = async () => {
    try {

      let finalContent = content;
      
      if (category === 'Members' || category === 'Alumni') {
        const memberData = {
          name: memberName,
          email: memberEmail,
          degree: memberDegree,
          image: `/api/v1/images/${memberImage}`
        };
        finalContent = JSON.stringify(memberData);
      }

      const requestBody = {
        title,
        content: finalContent,
        category,
        boardImageList: uploadedUrls
      };

      const response = await postBoardRequest(requestBody, cookies.accessToken);
      
      if (response && response.code === 'SU') {
        alert(" .");
        if (category === 'Members') {
          navigate('/member/members');
        } else if (category === 'Alumni') {
          navigate('/member/alumnis');
        } else {
          navigate(-1);
        }
        resetBoard();
      } else {
        alert("  .");
      }
    } catch (error) {
      console.error("  :", error);
      alert("  .");
    }
  };

  //     
  useEffect(() => {
    const accessToken = cookies.accessToken;
    if (!accessToken) {
      navigate('/');
      return;
    }
    resetBoard();
  }, [cookies, navigate, resetBoard]);

  // category   title    useEffect 
  useEffect(() => {
    if (category === 'Members') {
      setTitle('Members');
    } else if (category === 'Alumni') {
      setTitle('Alumni');
    } else if (category === 'Paper') {
      setTitle('Paper');
    }
  }, [category]);

  // Quill     
  const isQuillCategory = (cat) => {
    return !['Members', 'Alumni', 'Paper'].includes(cat);
  };

  //   
  const handleCategoryChange = (e) => {
    const newCategory = e.target.value;
    const oldCategory = category;
    setCategory(newCategory);

    //      Quill  
    if (isQuillCategory(oldCategory) && isQuillCategory(newCategory)) {
      const quillEditor = quillRef.current.getEditor();
      if (quillEditor) {
        //    
        const currentContent = quillEditor.getContents();
        quillEditor.setContents([]);
        setTimeout(() => {
          quillEditor.setContents(currentContent);
        }, 0);
      }
    }
  };

  return (
    <div className="flex flex-col">
      <div className="flex-1 custom-mb:px-4 px-2 py-8">
        <div className="max-w-6xl mx-auto">
          <div style={{ width: "100%", height: "800px" }}>
            <div style={{  margin: "auto", borderRadius: "19px" }}>
              <div
                style={{
                  marginBottom: "20px",
                  marginTop: "20px",
                  fontSize: "20px",
                  fontWeight: "bold",
                }}
              >
                
              </div>

              {/* ======== Subject ======== */}
              <div className="flex gap-4">
                <input
                  className="Subject"
                  placeholder="  "
                  style={{
                    padding: "7px",
                    marginBottom: "10px",
                    width: "100%",
                    border: "1px solid lightGray",
                    fontSize: "15px",
                    backgroundColor: category === 'Paper' || category === 'Members' || category === 'Alumni' ? '#f0f0f0' : 'white', //    
                  }}
                  value={title}
                  onChange={(e) => setTitle(e.target.value)}
                  disabled={category === 'Paper' || category === 'Members' || category === 'Alumni'} // Paper, Members, Alumni   
                />
                <select
                  className="Category"
                  style={{
                    padding: "7px",
                    marginBottom: "10px",
                    width: "20%",
                    border: "1px solid lightGray",
                    fontSize: "15px",
                  }}
                  value={category}
                  onChange={handleCategoryChange}
                >
                  <option value={null}> </option>
                  <option value="Notice">Notice</option>

                  <option value="Paper"></option>
                  <option value="Patent"></option>
                  <option value="Achievements">Achivements</option>


                  <option value="Members">Members</option>
                  <option value="Alumni">Alumni</option>
                 
                </select>
              </div>

              {/* Members Alumni      */}
              {(category === 'Members' || category === 'Alumni') ? (
                <div className="member-inputs space-y-4 p-6 bg-gray-50 rounded-lg">
                  <div className="mb-6">
                    <div className="text-sm text-gray-500 mb-4">
                      <span className="text-red-500">*</span>    
                    </div>
                    <div className="mb-4">
                      <label className="block text-gray-700 text-sm font-bold mb-2">
                          <span className="text-red-500">*</span>
                      </label>
                      <input
                        type="file"
                        accept="image/*"
                        className="w-full p-2 border rounded bg-white"
                        onChange={async (e) => {
                          const file = e.target.files[0];
                          if (file) {
                            try {
                              const imageUrl = await uploadImage(file, cookies.accessToken);
                              setMemberImage(imageUrl);
                              setUploadedUrls([imageUrl]);
                              console.log('  URL:', imageUrl); //  
                            } catch (error) {
                              console.error('  :', error);
                            }
                          }
                        }}
                      />
                    </div>
                    <div className="mb-4">
                      <label className="block text-gray-700 text-sm font-bold mb-2">
                         <span className="text-red-500">*</span>
                      </label>
                      <input
                        className="w-full p-2 border rounded bg-white"
                        placeholder=" "
                        value={memberName}
                        onChange={(e) => setMemberName(e.target.value)}
                      />
                    </div>
                    <div className="mb-4">
                      <label className="block text-gray-700 text-sm font-bold mb-2">
                         <span className="text-red-500">*</span>
                      </label>
                      <input
                        className="w-full p-2 border rounded bg-white"
                        placeholder=" "
                        value={memberEmail}
                        onChange={(e) => setMemberEmail(e.target.value)}
                      />
                    </div>
                    <div>
                      <label className="block text-gray-700 text-sm font-bold mb-2">
                         <span className="text-red-500">*</span>
                      </label>
                      <select
                        className="w-full p-2 border rounded bg-white"
                        value={memberDegree}
                        onChange={(e) => setMemberDegree(e.target.value)}
                      >
                        <option value=""> </option>
                        {category === 'Members' ? (
                          <>
                            <option value="Part Time Ph.D. Students">Part Time Ph.D. Students</option>
                            <option value="Master Students">Master Students</option>
                            <option value="Ph.D. Students">Ph.D. Students</option>
                          </>
                        ) : (
                          <>
                            <option value="Ph.D.">Ph.D.</option>
                            <option value="M.S.">M.S.</option>
                          </>
                        )}
                      </select>
                    </div>
                  </div>
                </div>
              ) : category === 'Paper' ? (
                <div className="paper-inputs space-y-4 p-6 bg-gray-50 rounded-lg">
                  <div className="text-sm text-gray-500 mb-4">
                    <span className="text-red-500">*</span>    
                  </div>
                  <div className="mb-4">
                    <label className="block text-gray-700 text-sm font-bold mb-2">
                        <span className="text-red-500">*</span>
                    </label>
                    <input
                      className="w-full p-2 border rounded bg-white"
                      placeholder="  "
                      value={paperTitle}
                      onChange={(e) => setPaperTitle(e.target.value)}
                    />
                  </div>
                  <div className="mb-4">
                    <label className="block text-gray-700 text-sm font-bold mb-2">
                       <span className="text-red-500">*</span>
                    </label>
                    <input
                      className="w-full p-2 border rounded bg-white"
                      placeholder=" (,)  "
                      value={authors}
                      onChange={(e) => setAuthors(e.target.value)}
                    />
                  </div>
                  <div className="mb-4">
                    <label className="block text-gray-700 text-sm font-bold mb-2">
                       <span className="text-red-500">*</span>
                    </label>
                    <input
                      className="w-full p-2 border rounded bg-white"
                      type="date"
                      value={publishDate}
                      onChange={(e) => setPublishDate(e.target.value)}
                    />
                  </div>
                  <div>
                    <label className="block text-gray-700 text-sm font-bold mb-2">
                       
                    </label>
                    <input
                      className="w-full p-2 border rounded bg-white"
                      placeholder="   ()"
                      value={paperLink}
                      onChange={(e) => setPaperLink(e.target.value)}
                    />
                  </div>
                </div>
              ) : (
                <div className="min-h-[500px]" key={category}>
                  <ReactQuill
                    ref={quillRef}
                    modules={modules}
                    formats={formats}
                    value={content}
                    placeholder="  "
                    onChange={(value) => setContent(value)}
                    style={{ height: "600px" }}
                  />
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
      <div className="sticky bottom-0 bg-white pb-12 pt-4 px-8 shadow-md">
        <div className="max-w-6xl mx-auto text-right">
          <button
            onClick={handleSubmit}
            className="px-4 py-2 bg-[#023793] text-white rounded hover:bg-[#034ABC] transition-colors"
          >
             
          </button>
        </div>
      </div>
    </div>
  );
}