import React, { useState, useEffect, useMemo, useCallback } from 'react';
import { useNavigate, useParams, useLocation } from 'react-router-dom';
import { useCookies } from 'react-cookie';
import ReactQuill from 'react-quill-new';
import 'react-quill-new/dist/quill.snow.css';
import { getBoardDetail, putBoardRequest, uploadImage } from '../apis';

export default function BoardEdit() {
  //   state
  const [title, setTitle] = useState('');
  const [content, setContent] = useState('');
  const [category, setCategory] = useState('');
  const [uploadedUrls, setUploadedUrls] = useState([]);


  const [cookies] = useCookies(['accessToken']);
  const navigate = useNavigate();
  const { boardNumber } = useParams();
  const quillRef = React.useRef();
  const location = useLocation();

  const imageHandler = useCallback(() => {
    const input = document.createElement('input');
    input.setAttribute('type', 'file');
    input.setAttribute('accept', 'image/*');
    
    input.onchange = async () => {
      const file = input.files[0];
      if (file) {
        try {
          const imageUrl = await uploadImage(file, cookies.accessToken);
          if (imageUrl) {
            const quillEditor = quillRef.current.getEditor();
            const range = quillEditor.getSelection(true);
            
            
            //    URL
            const fullUrl = `https://jbnuvetmedimmunelab.store${imageUrl}`;
            quillEditor.insertEmbed(range.index, 'image', fullUrl);
            
            //   
            const image = quillEditor.root.querySelector(`img[src="${fullUrl}"]`);
            if (image) {
              image.style.maxWidth = '100%';
              image.style.height = 'auto';
            }
            
            // uploadedUrls 
            setUploadedUrls(prev => [...prev, imageUrl]);
          }
        } catch (error) {
          console.error('  :', error);
          alert('  .');
        }
      }
    };
    input.click();
  }, [cookies.accessToken]);

  const modules = useMemo(() => ({
    toolbar: {
      container: [
        [{ header: [1, 2, 3, false] }],
        ["bold", "italic", "underline", "strike"],
        ["blockquote", "code-block"],
        [{ list: "ordered" }, { list: "bullet" }],
        [{ align: [] }],
        ["link", "image"],
        [{ size: ["small", false, "large", "huge"] }],
        ["clean"]
      ],
      handlers: {
        image: imageHandler
      }
    }
  }), [imageHandler]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await getBoardDetail(boardNumber);
        if (response) {
          const currentCategory = location.state?.category || response.category || 'Notice';
          setCategory(currentCategory);
          
          if (currentCategory === 'Professor') {
            setContent(location.state.fixedProfile + response.content);
          } else if (currentCategory === 'Introduction') {
            setContent(response.content);
          } else {
            setContent(response.content);
          }
          setTitle(response.title);
        }
      } catch (error) {
        console.error('  :', error);
        alert('   .');
      }
    };
    
    if (boardNumber) {
      fetchData();
    }
  }, [boardNumber, location.state]);

  const handleSubmit = async () => {
    try {
      if (!title || !content || !category) {
        alert(', ,   .');
        return;
      }

      let finalContent = content;
      

      const requestBody = {
        title,
        content: finalContent,
        category,
        boardImageList: uploadedUrls
      };

      const response = await putBoardRequest(boardNumber, requestBody, cookies.accessToken);

      if (response.code === 'SU') {
        alert(' .');
        //  
        switch (category) {
          case 'Professor':
            navigate('/professor');
            break;
          case 'Paper':
            navigate('/publication/paper');
            break;
          case 'Notice':
            navigate('/notice');
            break;
          case 'Introduction':
            navigate('/introduction');
            break;
          default:
            navigate(`/boardDetail?board_number=${boardNumber}`);
        }
      } else {
        alert('  .');
      }
    } catch (error) {
      console.error('   :', error);
      alert('  .');
    }
  };

  return (
    <div className="max-w-6xl mx-auto px-4 py-8">
      <div className="text-2xl font-bold mb-4"> </div>
      
      <div className="mt-8">
        <div className="flex gap-4 mb-4">
          <input
            className="flex-1 p-2 border rounded"
            placeholder=""
            value={title}
            onChange={(e) => setTitle(e.target.value)}
          />
          <select
            value={category}
            onChange={(e) => setCategory(e.target.value)}
            className="w-32 p-2 border rounded bg-white"
          >
            <option value=""></option>
            <option value="Notice"></option>
            <option value="Research"></option>
            <option value="Paper"></option>
            <option value="Members"></option>
            <option value="Alumni"></option>
            <option value="Professor"></option>
            <option value="Introduction"> </option>
          </select>
        </div>

        <div className="min-h-[600px] mb-12">
          <ReactQuill
            ref={quillRef}
            value={content}
            onChange={setContent}
            modules={modules}
            className="h-[600px]"
            style={{ height: '600px' }}
          />
        </div>

        <div className="flex justify-end gap-4 mt-6">
          <button
            onClick={() => navigate(-1)}
            className="px-6 py-2.5 rounded text-gray-600 font-medium hover:bg-gray-100 transition-colors border border-gray-300"
          >
            
          </button>
          <button
            onClick={handleSubmit}
            className="px-6 py-2.5 rounded bg-[#023793] text-white font-medium hover:bg-[#034ABC] transition-colors"
          >
            
          </button>
        </div>
      </div>
    </div>
  );
} 